<template>
  <div class="container row contact">
    <div class="col-6 contact_img"></div>

    <ValidationObserver action="" class="col-6 contact_form" ref="form" tag="form">
      <h2>Contact</h2>

      <validation-provider rules="required" name="" v-slot="{ errors }" tag="div"
        class="form-group">
        <label for="name"><span class="text-danger">*</span></label>
        <input type="text" id="name" placeholder="" v-model="message.name">
        <span class="text-danger">{{ errors[0] }}</span>
      </validation-provider>

      <validation-provider rules="email|required" name="" v-slot="{ errors }" tag="div"
        class="form-group">
        <label for="email"><span class="text-danger">*</span></label>
        <input type="text" id="email" placeholder="" v-model="message.email">
        <span class="text-danger">{{ errors[0] }}</span>
      </validation-provider>

      <validation-provider rules="phone|required" name="()" v-slot="{ errors }" tag="div"
        class="form-group">
        <label for="tel"><span class="text-danger">*</span>()</label>
        <input type="number" id="tel" placeholder="()" v-model="message.tel">
        <span class="text-danger">{{ errors[0] }}</span>
      </validation-provider>

      <validation-provider rules="required" name="" v-slot="{ errors }" tag="div"
        class="form-group">
        <label for="title"><span class="text-danger">*</span></label>
        <input type="text" id="title" placeholder="" v-model="message.title">
        <span class="text-danger">{{ errors[0] }}</span>
      </validation-provider>

      <validation-provider rules="required" name="" v-slot="{ errors }" tag="div"
        class="form-group">
        <label for="content"><span class="text-danger">*</span></label>
        <textarea name="content" id="content" rows="10" placeholder=""
         v-model="message.content"></textarea>
        <span class="text-danger">{{ errors[0] }}</span>
      </validation-provider>

      <button type="submit" class="btn btn-l btn-primary rounded" @click.prevent="leaveMessage()">
        
      </button>

    </ValidationObserver>
  </div>
</template>

<script>
export default {
  data() {
    return {
      message: {
        name: '',
        email: '',
        tel: '',
        title: '',
        content: '',
      },
    };
  },
  methods: {
    leaveMessage() {
      const vm = this;
      vm.$refs.form.validate().then((success) => {
        if (success) {
          const alert = {
            isShow: true,
            title: '!',
            content: '',
            to: '/',
            status: 'success',
          };
          this.$store.dispatch('alertModules/openAlert', alert);
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@import '@/assets/_mixin.scss';
@import '@/assets/_variable.scss';

.contact {
  padding: 1rem 0 2.5rem;
  @include desktop {
    padding: 1rem 2rem 2.5rem 2rem;
  }
  @include mobile-l {
    padding: 1rem 1rem 2.5rem 1rem;
  }
}

.contact_img {
  min-height: 19rem;
  background: url('../assets/images/contact.jpg') center no-repeat;
  background-size: cover;
  @include mobile-m {
    min-height: 12.5rem;
  }
}

.contact_form {
  h2 {
    text-align: center;
    letter-spacing: 0.2rem;
  }
  button {
    width: 100%;
  }
}
</style>



